import React from "react";
import {
  Layout,
  Menu,
  Input,
  Avatar,
  Badge,
  Card,
  Progress,
  Row,
  Col,
  Typography,
  Button,
} from "antd";
import {
  BellOutlined,
  UserOutlined,
  SearchOutlined,
  PlayCircleOutlined,
  FireOutlined,
} from "@ant-design/icons";
import { logo, hinh1, hinh2, hinh3 } from "../assets/index";
import "./Home.css";

const { Header, Content, Footer } = Layout;
const { Title, Text } = Typography;

const Home = () => {
  // D liu mu: myCourses c y  description, endDate, completedLessons, totalLessons
  const myCourses = [
    {
      id: 1,
      title: "ReactJS c bn",
      description: "Hc cch xy dng giao din ReactJS t c bn n nng cao.",
      progress: 60,
      completedLessons: 2,
      totalLessons: 3,
      endDate: "30/06/2025",
      image: hinh1,
    },
    {
      id: 2,
      title: "Kha hc Java nng cao",
      description: "Khm ph Java qua cc d n thc t t c bn n nng cao.",
      progress: 40,
      completedLessons: 4,
      totalLessons: 10,
      endDate: "15/07/2025",
      image: hinh2,
    },
  ];

  const recommendedCourses = [
    {
      id: 3,
      title: "UI/UX Design",
      description: "Thit k giao din p mt, thu ht ngi dng.",
      image: hinh3,
    },
    {
      id: 4,
      title: "Python c bn",
      description: "Khm ph Python qua cc d n thc t t c bn n nng cao.",
      image: hinh1,
    },
  ];

  const upcomingCourses = [
    { id: 5, title: "Machine Learning", date: "15/06/2025", image: hinh2 },
    { id: 6, title: "Data Science", date: "22/06/2025", image: hinh3 },
  ];

  const fields = ["Lp trnh", "Thit k", "Kinh doanh", "Pht trin bn thn"];

  return (
    <Layout>
      {/* Header */}
      <Header>
        <img src={logo} alt="Logo" />
        <Menu mode="horizontal" defaultSelectedKeys={["1"]} style={{ flex: 1 }}>
          <Menu.Item key="1">Trang ch</Menu.Item>
          <Menu.Item key="2">Kha hc  hon thnh</Menu.Item>
          <Menu.Item key="3">Lnh vc</Menu.Item>
          <Menu.Item key="4">H tr</Menu.Item>
        </Menu>
        <Input
          placeholder="Tm kim..."
          prefix={<SearchOutlined />}
          style={{ width: 220, marginRight: 20, borderRadius: 20 }}
        />
        <Badge count={3} style={{ marginRight: 20 }}>
          <BellOutlined style={{ fontSize: 20, color: "#252626" }} />
        </Badge>
        <Avatar icon={<UserOutlined />} />
      </Header>

      {/* Content */}
      <Content className="site-content">
        {/* Banner */}
        <div className="site-banner">
        <Title
          style={{
            color: "#fff",
            textShadow: "0 0 8px rgba(0,0,0,0.3)",
            animation: "textPulse 2s infinite",
          }}
        >
          Cho mng bn n vi nn tng hc tp trc tuyn
        </Title>

          <p>
            Khm ph hng trm kha hc hp dn v pht trin k nng ca bn!
          </p>
          <Button type="primary" size="large">
            Bt u hc ngay
          </Button>
        </div>

        {/* Gii thiu */}
        <div className="site-intro">
          <Title level={3}>Ti sao nn chn chng ti?</Title>
          <p>
            Chng ti cung cp hng trm kha hc a dng, ging vin cht
            lng v cng ng hc tp si ni. Nng cao k nng ca bn ngay
            hm nay!
          </p>
        </div>

        {/* Cc lnh vc */}
        <div className="course-fields">
          <Title level={3}>Lnh vc kha hc</Title>
          <Row gutter={16}>
            {fields.map((field, index) => (
              <Col span={6} key={index}>
                <Card hoverable>{field}</Card>
              </Col>
            ))}
          </Row>
        </div>

        {/* Kha hc ca ti */}
        <Title level={3} style={{ marginTop: 24 }}>
          Kha hc ca ti
        </Title>
        <Row gutter={16} className="my-courses">
          {myCourses.map((course) => (
            <Col span={8} key={course.id} style={{ position: "relative" }}>
              {/* Attention badge */}
              <div className="attention-badge">
                <FireOutlined style={{ marginRight: 4 }} />
                Hc ngay!
              </div>

              <Card
                cover={<img alt={course.title} src={course.image} />}
                actions={[
                  <Button type="link" icon={<PlayCircleOutlined />}>
                    Tip tc hc
                  </Button>,
                ]}
              >
                <Card.Meta
                  title={course.title}
                  description={course.description}
                />
                <div style={{ marginTop: 12 }}>
                  <Text strong>
                     hon thnh: {course.completedLessons}/
                    {course.totalLessons} bi
                  </Text>
                  <Progress percent={course.progress} status="active" />
                  <Text type="secondary">
                    Hn kt thc: {course.endDate}
                  </Text>
                </div>
              </Card>
            </Col>
          ))}
        </Row>

        {/* Gi  kha hc */}
        <Title level={3} style={{ marginTop: 24 }}>
          Kha hc gi 
        </Title>
        <Row gutter={16} className="recommended-courses">
          {recommendedCourses.map((course) => (
            <Col span={8} key={course.id}>
              <Card
                cover={<img alt={course.title} src={course.image} />}
                hoverable
                style={{ background: "#e6f7ff" }}
              >
                <Card.Meta
                  title={course.title}
                  description={course.description}
                />
                <Button type="primary" style={{ marginTop: 8 }}>
                  ng k ngay
                </Button>
              </Card>
            </Col>
          ))}
        </Row>

        {/* Kha hc sp ti */}
        <Title level={3} style={{ marginTop: 24 }}>
          Kha hc sp ti
        </Title>
        <Row gutter={16} className="upcoming-courses">
          {upcomingCourses.map((course) => (
            <Col span={8} key={course.id}>
              <Card
                cover={<img alt={course.title} src={course.image} />}
                title={course.title}
              >
                <p>Ngy bt u: {course.date}</p>
                <Button type="dashed">Xem chi tit</Button>
              </Card>
            </Col>
          ))}
        </Row>
      </Content>

      {/* Footer */}
      <Footer>
        2025 Nn tng Hc Trc Tuyn | Lin h: support@hoc.com | Hotline:
        0123-456-789
      </Footer>
    </Layout>
  );
};

export default Home;
